.sidebar {
    height: 100vh !important;
    background-color: #1e1e1e; /* Dark background color */
    color: #fff; /* Text color */
    overflow-y: auto; /* Add scrollbar for overflow */
    transition: width 0.3s ease, background-color 0.3s ease; /* Smooth width and background color transitions */
  }
  
  .menu {
    padding-top: 50px; /* Adjust top padding */
  }
  
  .menu-item {
    padding-top: 20px; /* Adjust top padding */
    transition: transform 0.3s ease; /* Smooth transform transition */
  }
  
  .menu-item:hover {
    transform: translateX(5px); /* Move menu item to the right on hover */
  }
  
  .menu-item a {
    color: #ccc; /* Text color */
    text-decoration: none;
    font-size: 1.2rem;
  }
  
  .menu-item i {
    font-size: 1.2rem;
    margin-right: 10px; /* Adjust margin for better spacing */
  }
  
  .active {
    background-color: #2e86de; /* Active item background color */
    padding: 10px 20px; /* Adjust padding */
    margin-top: 20px; /* Adjust margin */
    border-radius: 5px; /* Add border radius */
  }
  
  .active a,
  .active i {
    color: #fff; /* Text color for active item */
  }
  