.admin-home-container {
    max-width: 800px;
    margin: 0 auto;
  }
  
  .admin-heading {
    font-size: 2.5rem;
    font-weight: bold;
    color: #343a40;
  }
  
  .admin-name {
    color: #28a745; /* Green color for the admin name */
  }
  
  .admin-subheading {
    font-size: 1.5rem;
    font-weight: bold;
    color: #007bff; /* Blue color for the subheading */
    margin-top: 1.5rem;
  }
  
  .admin-hr {
    border-top: 2px solid #28a745; /* Green color for the hr */
    width: 100px;
    margin-top: 10px;
  }
  
  .admin-paragraph {
    font-size: 1.1rem;
    line-height: 1.6;
    margin-top: 1.5rem;
    color: #6c757d; /* Dark gray color for paragraphs */
  }
  