/* Form title */
.form-title {
    font-size: 2.5rem; /* Increased font size */
    font-weight: bold;
    color: #333;
    margin-bottom: 20px;
    text-align: center;
    /* text-transform: uppercase; Uppercase text */
    letter-spacing: 2px; /* Increased letter spacing */
    font-family: 'Poppins', sans-serif; /* Changed font family */
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1); /* Text shadow for depth */
    animation: fadeInUp 1s ease infinite alternate, colorChange 5s infinite; /* Continuous animation for entry and color change */
  }
  
  /* Entry animation keyframes */
  @keyframes fadeInUp {
    0% {
      opacity: 0;
      transform: translateY(50px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  /* Color change animation keyframes */
  @keyframes colorChange {
    0% {
      color: #333;
    }
    50% {
      color: #007bff; /* Blue color */
    }
    100% {
      color: #333;
    }
  }
  
  
  
  
  /* Horizontal rule */
  .custom-hr {
    border-top: 2px solid #007bff; /* Blue color for hr */
    width: 50px;
    margin: 20px auto;
  }
  
  /* Radio button container */
  .radio-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
  }
  
  /* Radio button label */
  .radio-label {
    font-size: 1rem;
    color: #555;
    margin-left: 10px;
  }
  
  /* Input fields */
  .input-field {
    margin-bottom: 20px;
  }
  
  /* Label for input fields */
  .input-label {
    font-size: 1rem;
    color: #555;
    margin-bottom: 5px;
  }
  
  /* Input field */
  .input-element {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    transition: border-color 0.3s ease-in-out;
  }
  
  .input-element:focus {
    outline: none;
    border-color: #007bff; /* Blue color on focus */
  }
  
  /* Submit button container */
  .submit-button-container {
    text-align: center;
  }
  
  /* Submit button */
  .submit-button {
    background-color: #007bff; /* Blue color for button */
    color: #fff;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
  }
  
  .submit-button:hover {
    background-color: #0056b3; /* Darker blue on hover */
  }
  
  /* Link for switching between login and register */
  .switch-link {
    font-size: 1rem;
    color: #007bff; /* Blue color for link */
    text-decoration: none;
    transition: color 0.3s ease-in-out;
  }
  
  .switch-link:hover {
    color: #0056b3; /* Darker blue on hover */
  }
