.custom-card {
    background-color: #f7f7f7; /* Default background color */
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease;
  }
  
  .custom-card:hover {
    background-color: #e9ecef; /* Change background color on hover */
    transform: scale(1.02); /* Scale effect on hover */
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2); /* Increase shadow on hover */
  }
  
  .card-title {
    padding: 15px 0;
    font-size: 1.5rem;
    font-weight: bold;
  }
  
  .card-body, .card-footer {
    padding: 10px;
  }
  
  .card-footer {
    background-color: #343a40; /* Footer background color */
  }
  
  .card-footer b {
    font-weight: bold;
  }
  
  .card-footer, .card-footer b {
    color: #ffffff; /* Footer text color */
  }
/* Custom styling for the "Recent Blood Transactions" heading */
