*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /* overflow: hidden; */
}
.form-banner img{
   height: 100%;
   width: 100%;
}
.form-banner {
  background-color: #f0f0f0;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  max-width: 800px;
  width: 100%;
}
.form-container{
 
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
  
}
.form-title {
  text-align: center;
  margin-bottom: 20px;
  color: #333; /* Change title color */
  font-family: 'Arial', sans-serif; /* Change font family */
  font-size: 24px; /* Adjust font size */
}
.form-title.animated {
  animation: slideInDown 0.5s ease; /* Apply animation */
}

@keyframes slideInDown {
  from {
    transform: translateY(-100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
/* 
------loader ---
-----------------
*/
.loader-container{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color:rgba(0, 0, 0, 0.3);
}
.loader{
  width:48px ;
  height: 48px;
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;

}
@keyframes rotation {
  0%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(360deg);
  }
}

/* 
------navbar------

*/


.navbar {
  background-color: black !important;
  color: white !important;
  padding: 15px 0; /* Add padding for better spacing */
  transition: background-color 0.3s ease; /* Smooth transition for background color */
}

.navbar-brand {
  color: white !important;
  font-size: 24px; /* Increase font size for better visibility */
}

.nav-link {
  color: white !important;
  font-size: 18px; /* Adjust font size */
  transition: color 0.3s ease; /* Smooth transition for text color */
}

.nav-link:hover {
  color: #ffc107 !important; /* Change text color on hover */
}

.nav-link.active {
  color: #ffc107 !important; /* Change text color for active link */
}

.nav-link::after {
  content: '';
  display: block;
  width: 0;
  height: 2px;
  background: #ffc107; /* Add underline effect */
  transition: width 0.3s ease;
}

.nav-link:hover::after {
  width: 100%; /* Expand underline on hover */
}

/* Add animation for navbar on page load */
@keyframes slideInDown {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0);
  }
}

.navbar {
  animation: slideInDown 0.5s ease;
}

/* 
---submit button-----

*/
.submit-button-container {
  text-align: center;
}

.submit-button {
  background-color: #007bff;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-family: 'Arial', sans-serif; /* Change font family */
  font-size: 16px; /* Adjust font size */
  cursor: pointer;
  transition: background-color 0.3s ease; /* Smooth transition */
}

.submit-button:hover {
  background-color: #0056b3; /* Darker shade on hover */
}

/* Button Animation */
.submit-button {
  animation: pulse 1s infinite alternate;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.05);
  }
}
